body {
  margin: 0;
  box-sizing: border-box;
  font-family: Roboto;
  background: #f3f3f3;
}

.App {
  text-align: center;
  width: 100vw;
  height: 25vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.datePicker {
  width: 250px;
  height: 50px;
  border: 1px solid black;
  display: flex;
  align-items: center;
  padding: 2px 20px;
  justify-content: space-between;
  font-size: 14px;
}

.datePicker:hover {
  border: 2px solid black;
}

.datePicker:hover,
.datePicker > *:hover {
  cursor: default;
}

.datePicker > div {
  padding: 0;
}

.icon {
  width: 18px;
}

.calendar-holder {
  position: relative;
}

.calendar {
  width: 520px;
  background: #fff;
  padding: 16px;
  position: absolute;
  z-index: 600;
  left: 0;
  top: 12px;
  border: 0;
  border-radius: 4px;
  -webkit-box-shadow: 0 2px 16px rgba(0, 0, 0, 0.15);
  box-shadow: 0 2px 16px rgba(0, 0, 0, 0.15);
  margin: 0;
  transition: 0.4s;
}

.calendar__content {
  display: flex;
  justify-content: space-between;
}

.calendar__wrapper {
  margin: 0;
  width: 250px;
}

.calendar__month {
  font-weight: 700;
  text-align: center;
  margin-bottom: 8px;
  font-size: 16px;
  line-height: 1.5em;
}

.calendar__days {
  display: flex;
}

.calendar__body {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.calendar__dates {
  display: flex;
  flex-direction: column;
  width: 100%;
  font-weight: 400;
}

.calendar__day--name,
.calendar__date {
  font-weight: 400;
  color: #707070;
  font-size: 14px;
}

.calendar__day--name {
  width: 14%;
}

.calendar__head {
  margin-bottom: 15px;
}

.calendar__date {
  width: 14%;
  height: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.calendar__date--past {
  background: #fff;
  color: #000;
  opacity: 0.5;
  pointer-events: none;
}

.calendar__date:hover {
  background: #07c;
  color: #fff;
  cursor: default;
}

.calendar__date--empty {
  pointer-events: none;
}

.calendar__date.selected {
  background: #07c;
  color: #fff;
}

.calendar__date.calendar__date--past,
.calendar__date.calendar__date--empty {
  background: #fff;
}

.hightlight .selected ~ .calendar__date {
  color: #707070;
  background: #ededed;
}

.calendar__date ~ .selected {
  background: #07c;
  color: #fff;
}

.selected[data-position="end"] ~ .calendar__date {
  background: #fff;
}

.hightlight .calendar__date.selected,
.calendar__date.calendar__date--future:hover {
  background: #07c;
  color: #fff;
}
